.container {
  width: 100%;
  height: 100%;
  display: flex;
  cursor: pointer;
  --app-theme-color: rgb(255, 77, 79);

  .mainPanel {
    background-color: #fff;
    align-items: center;
    width: 100%;
    height: 100%;
    flex-flow: wrap;
    flex-shrink: 0;
    display: flex;

    .miniHeader {
      width: 100%;
      text-align: center;
      line-height: 28px;
      background-color: var(--app-theme-color);
      color: #fff;
    }

    &.needSubPanel {
      align-content: start;
      text-align: left;
      border-right: 1px solid rgba(0, 0, 0, 0.05);
      background-color: rgb(248, 248, 248);
    }
  }

  .subPanel {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-flow: wrap;
    width: 100%;
    height: 100%;

    .calendar {

      .calendarWeek,
      .calendarDays {
        &>div>span {
          width: 24px;
          height: 24px;
        }
      }
    }
  }
}

.calendar {
  width: 100%;
  height: 100%;
  padding: 4%;

  .calendarWeek,
  .calendarDays {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;

    &>div {
      width: 14.2857%;
      display: flex;
      justify-content: center;
      align-items: center;

      &:nth-child(7n),
      &:nth-child(7n + 1) {
        color: var(--app-theme-color);
      }

      span {
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;

        &.curDay {
          color: #fff;
          background-color: var(--app-theme-color);
          border-radius: 100%;
        }
      }
    }
  }

  .calendarWeek {
    color: rgb(28, 28, 28);
    font-weight: 600;
  }

  .calendarDays {
    color: rgb(142, 142, 142);
  }
}

.iconWrap {
  display: flex;
  width: 100%;
  font-size: 36px;
  span {
    padding: 4px;
    background: var(--app-theme-color);
    border-radius: 4px;
    color: #fff;
  }
}


.flex {
  display: flex;
}

.fw-nowrap {
  flex-wrap: nowrap;
}

.row {
  flex-direction: row !important;
}

.column {
  flex-direction: column !important;
}

.flex-grow {
  flex-grow: 1;
}

.jc-center {
  justify-content: center;
}

.jc-evenly {
  justify-content: space-evenly;
}

.jc-between {
  justify-content: space-between;
}

.jc-start {
  justify-content: flex-start;
}

.jc-end {
  justify-content: flex-end;
}

.ai-center {
  align-items: center;
}

.ai-baseline {
  align-items: baseline;
}

.ac-start {
  align-content: flex-start;
}

.f12 {
  font-size: 12px;
}

.f14 {
  font-size: 14px;
}

.f16 {
  font-size: 16px;
}

.f24 {
  font-size: 24px;
}

.f36 {
  font-size: 36px;
}

.w-full {
  width: 100%;
}

.f48 {
  font-size: 48px;
}

.tc-theme-color {
  color: var(--app-theme-color);
}
